#root{
  width: 100%;
  height: 100%;
}
a{
  text-decoration: none;
  color: white;
}
p{
  margin: 0;
}
.Content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  
}
/*NavBar*/
.NavBar{
  flex: 1;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: rgb(104, 104, 104);
}
.NavEl{
  height: 100%;
  align-items: center;
  display: flex;
  flex-direction: row;
  flex:1;
}

.NameEl{
  height: 100%;
  flex:1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.NavBar {
  color: white;
}

.NavElWrapper{
  padding-left: 2%;
}
.NameWrapper{
  padding-right: 6%;
}
.NavElWrapper p:hover{
  border-bottom: 1px solid rgb(255, 254, 254);
  cursor: pointer;
}
.NavElWrapper a:hover{
  border-bottom: 1px solid rgb(255, 254, 254);
  cursor: pointer;
}


/*Main Content*/
.MainContent{
  display: flex;
  flex-direction: column;
  width: 100%;
  flex:16;
}

.Row{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.Row div{
  height: 100%;
  width: 100%;
}

.TextContent{
  width:70%;
  height:90%;
  font-size: 16px;
  color: transparent;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  text-align: center;
  text-decoration: none;
}
.Header{
  position: relative;
}
.ContentWrapper{
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*footer*/
#contactDiv{
  flex: 1;
  width: 100%;
  background-color: rgb(104, 104, 104);
  display: flex;
}
#contactDiv p{
  color: white;
}
.ContactWrapper{
  align-items: center;
  display: flex;
  flex-direction: row;
  width: 100%;
}
.ContactWrapper p{
  padding-left: 2%;
}
.ContactWrapper a{
  padding-left: 2%;
}

/*about me page*/
  .AboutMeComp{
    height: 80vh;
    width:100%;
    display:flex;
  }
  .AboutMeWrapper > p{
    height: fit-content;
    width: 80%;
    color: black;
  }
  .AboutMeWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .Picture{
    padding-left: 3%;
    height: 60vh;
    width: 60vh;
    border-radius: 100%;
  }
  .PictureWrapper{
    display:flex;
    justify-content: center;
    align-items: center;
  }


@media only screen 
    and (min-width : 414px) 
    and (max-height : 736px)
    and (orientation : portrait)  
    and (-webkit-device-pixel-ratio : 3) {
        .NavBar {
          color: blueviolet;
        }

     }
